/*Mobile*/
.icon-home {
    width: 12px;
    height: 12px;
}
.reviews  {
    font-family: var(--DM-Sans-font);
    padding-bottom: 300px;
}
.reviews-box {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.reviews-content img { 
    display: block;
    width: 85px;
    height: 85px;
}
.reviews-content {
    display: flex;
   flex-direction: column;
   align-items: center;
}
.test {
    display: none;
}
/*Tablet*/
@media (min-width: 768px) {
    .reviews{
    padding-bottom: 250px;
}
}
/*Desktop*/
@media (min-width: 1200px) {
    .reviews {
        padding-bottom: 100px;
    }
}